    text-align: center;
    float: right;
}

.copyTxt {
    width: 40px;
    font-size: 12px;
    height: 20px;
    line-height: 20px;
    color: white;
    background: #108ee9;
    margin: 0 auto;
}

.listItemDiv {
    width: 100%;
    text-align: center;
    color: #3c3c3c;
    font-size: 12px;
}

.listItemDiv .l {
    width: 20%;
    float: left;
    display:block;
    /*width:31em;*/
    word-break:keep-all;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
}

.sub-title {
    color: #888;
    font-size: 14px;
    padding: 15px 0 9px 15px;
}
.not-square-grid .am-grid-icon {
    width: 30px;
    height: 30px;
}

.divcss5 {
    margin: 0px 30px 0px 0px;
    /*border: 1px solid #00F;*/
    /*height: 120px;*/
    line-height: 1;
    width: 80%;
    word-wrap: break-word;
}
.my-drawer {
    position: relative;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar {
    background-color: #fff;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}

.my-drawer .am-drawer-sidebar .am-list {
    width: 300px;
    padding: 0;
}

.am-drawer.am-drawer-left .am-drawer-draghandle, .am-drawer.am-drawer-right .am-drawer-draghandle {
    width: 0px !important;
    height: 100%;
}

.flex-container {
    margin: 0 15px;
}

.flex-container .inline {
    width: 80px !important;
    margin: 9px 9px 9px 0;
}

.flex-container .small {
    height: 20px !important;
    line-height: 20px !important;
}

.sub-title {
    color: #888;
    font-size: 14px;
    padding: 10px 0 15px 0;
}


.exchange .head1{
    font-size: 28px;
    font-weight: bold;
    color: green;
    background-color: white;
    align-items: center;
    padding: 15px;
    letter-spacing:3px;
}


